<template>

  <div>
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="$router.back()" />
    <div class="list" ref="list">
      <router-link :to="`/product/${item.id}`" class="item" v-for="(item, index) in list" :key="`product-${item.id}`">
        <img v-lazy="item.cover">
        <h3>{{item.name}}</h3>
        <div class="price clearfix">
          <span class="price-l">￥</span>
          <span class="cprice">{{item.price}}</span>
          <span class="price-r">起</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        list:[]
      }
    },
    created () {
      this.list = this.$store.user.likeList ? this.$store.user.likeList : []
    },
    mounted () {
      this.$axios.get('/product_like/all').then(res => {
        if(res.errcode == 0){
          this.list = res.data
        }
      })
    },
    methods: {
      
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .list {
    background-color: white;
    height: calc(100vh - 50px);
  }

  .van-loading {
    text-align: center;
    padding-bottom: 20px;
  }

  .my-tab {
    height: 10vw;
    width: 100vw;
    display: flex;
    align-items: center;
    overflow-y: hidden;
    flex-wrap: nowrap;
  }

  .my-tab a {
    display: inline-block;
    padding: 0 20vw;
    text-align: center;
    line-height: 10vw;
    white-space: nowrap;
  }

  .van-swipe {
    margin-bottom: 10px;
  }

  .van-swipe-item img {
    width: 100%;
    height: 40vw;
  }

  .van-icon-search {
    font-size: 5vw;
  }
</style>


